import { computed, defineComponent, ref } from 'vue';
import ListComp from '@/components/list/index.vue';
import ClientQrCodeComp from '@/components/client-qr-code/index.vue';
import UserInfoComp from '@/components/user-info/index.vue';
import useHttpRequest from '@/utils/request';
import { ElMessage } from 'element-plus';
import { useStore } from 'vuex';
import router from '@/router';
export default defineComponent({
    components: {
        ListComp: ListComp,
        ClientQrCodeComp: ClientQrCodeComp,
        UserInfoComp: UserInfoComp
    },
    setup: function () {
        var state = useStore().state;
        // 个人信息
        var userData = computed(function () {
            return state.user.simpleUserData;
        });
        // 列表数据获取
        var _a = useHttpRequest(), isLoading = _a.isLoading, adornUrl = _a.adornUrl, httpRequest = _a.httpRequest;
        var collect = ref([]);
        var getData = function () {
            if (userData.value && userData.value.loginname) {
                httpRequest({
                    url: adornUrl("/api/v1/topic_collect/" + userData.value.loginname),
                    method: 'get'
                }).then(function (_a) {
                    var data = _a.data;
                    collect.value = data.data;
                }).catch(function (e) {
                    ElMessage.error('请求失败');
                    console.error(e);
                });
            }
            else {
                router.push('/index');
            }
        };
        getData();
        // 查看详情
        var seeDetail = function (id) {
            router.push({
                path: "/detail",
                query: {
                    id: id,
                }
            });
        };
        return {
            isLoading: isLoading,
            collect: collect,
            seeDetail: seeDetail,
        };
    },
});
